#moreButton,
.button-ctg {
font-family: 'Manrope', sans-serif;
font-style: normal;
color: #4440F6;
background: #FFFFFF;
font-weight: 400;
font-size: 12px;
line-height: 1.33;
text-align: center;
padding: 7.5px 24.5px;
border-radius: 20px;
cursor: pointer;
border: 1px solid #4440F7;
transition-property: color, background-color;
transition-duration: 250ms;
transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
#moreButton:hover,
#moreButton:focus,
.button-ctg:hover,
.button-ctg:focus {
color: #FFFFFF;
background: #4440F6;
}
.button-container {
display: flex;
flex-wrap: nowrap;
flex-direction: row;
align-items: flex-start;
@media screen and (min-width: 768px) {
    gap: 14px;
}
@media screen and (min-width: 1280px) {
    gap: 16px;
}
}

#modal {
    z-index: 1;
    width: 100%;
    max-height: 80%;
    background-color: #0006;
}

.hidden {
    display: none;
}

#modalContent {
    z-index: 1;
    height: 380px;
    background-color: #FEFEFE;
    border-radius: 20px;
    flex-direction: column;
    align-content: space-between;
    gap: 12px;
    display: flex;
    position: absolute;
    overflow-y: auto;
    padding: 14px 24px;
    filter: drop-shadow(0px 10px 60px rgba(0, 0, 0, 0.1));
}
.more-item-categories {
    border: none;
    background: inherit;
    padding: 0px;
    color: #4440F6;
    &:hover {
        text-decoration-line: underline;
    }
}
#modalContent::-webkit-scrollbar {
    width: 0px;
}

.container-main {
    margin-top: 28px;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 768px) {
    .button-rend {
      display: none;
    }
  }
  
  @media (min-width: 768px) and (max-width: 1280px) {
    .button-rend:nth-last-child(-n+2) {
      display: none;
    }
  }
